main{
	overflow: hidden;
	max-width: 1440px;
	margin: auto;
	display: block;
}
.banner{
	.col-12{
		padding: 0;
	}
	.claimer{
		width: percentage(((751  * 100) / $width)* 0.01);
		position: absolute;
		top:0;
		left: 0;
		height: 100%;
		h1{
			color: #00a89c;
			margin: 30px 0 10px 15px;
			font-weight:800;
		}
		p{
			text-align: center;
			padding: 0 5px;
			font-size: 10px;
			font-weight: 500;
		}
		@media (min-width: 375px){
			h1{
				margin: 30px 0 20px 15px;
			}
			p{
				padding: 0 20px;
				font-size: 11px;
			}
		}
		@media (min-width: 420px){
			h1{
				margin: 30px 0 30px 15px;
				font-size: 25px;
			}
		}
	}
	@media (min-width: 720px){
		.container{
			max-width: 100%;
		}
		.claimer{
			h1{
				font-size: 46px;
				padding-right: 50px;
				line-height: 48px;
				margin: 100px 0 50px 30px;
			}
			p{
				width: 80%;
				display: block;
				margin: auto;
				font-size: 18px;
			}
		}
	}
	@media (min-width: 991px){
		.container{
			max-width: 100%;
		}
		.claimer{
			h1{
				font-size: 60px;
			    padding-right: 80px;
			    line-height: 58px;
			    margin: 140px 0 80px 30px;
			}
			p{
				font-size: 25px;
			}
		}
	}
	@media (min-width: 1200px){
		.claimer{
			h1{
				font-size: 90px;
			    line-height: 78px;
			    margin: 160px 0 120px 30px;
			}
			p{
				font-size: 34px;
			}
		}
	}
}
.background-covid{
	background: url('../images/fondo.png') #ffffff;
	background-size: cover;
	min-height: 500px;
	margin-top: -10px;
	padding: 15px 0;
	h2{
		color: #5b86b1;
		font-size: 14px;
		width: 100%;
		text-align: center;
		&::after{
			content: '';
			display: block;
			width: 60%;
			height: 4px;
			background-color: #00a89c;
			margin-left: 20%;
			margin-top: 10px;
		}
	}
	@media (min-width: 768px){
		h2{
			font-size:28px;
		}
	}
	@media (min-width: 991px){
		padding-bottom: 50px;
		h2{
			font-size: 40px;
			&::after{
				height: 6px;
			}
		}
	}
	@media (min-width: 1200px){
		.container{
			max-width: 1200px;
		}
		h2{
			font-size: 50px;
		}
	}
	.maquina-desinfectante{
		padding: 0 15px;
		p{
			font-size: 12px;
			&.title{
				color: #00a89c;
				font-weight: 500;
				text-align: center;
			}
		}
		ul{
			padding: 0 15px;
			li{
				font-size: 12px;
				text-align: center;
				img{
					margin-bottom: 10px;
				}
			}

		}
		.maquina{
			margin: 0 auto; 
			display: block;
			width: 50%;
		}
		.funciones{
			margin: 0 auto; 
			display: block;
			width: 90%;
		}
		@media (min-width: 768px){
			.maquina{
				width: 70%;
			}
			.funciones{
				width: 100%;
			}
			p{
				&.title{
					text-align: left;
				}
			}
			ul{
				padding: 0;
				li{
					text-align:left;
				}
				&.two-column{
					height: 400px;
					li{
						text-align:center;
						p{
							&.title{
								text-align: center;
							}
						}
						.icons{
							width: 20px;
						}
					}
				}

			}
		}
		@media (min-width: 991px){
			p{
				font-size: 14px;
				&.title{
					font-size: 16px;
				}
			}
			ul{
				li{
					font-size: 14px;
				}
				&.two-column{
					li{
						.icons{
							width: 20px;
						}
					}
				}
			}
		}
		@media (min-width: 1200px){
			p{
				font-size: 17px;
				&.title{
					font-size: 19px;
				}
			}
			ul{
				li{
					font-size: 17px;
				}
				&.two-column{
					li{
						.icons{
							width: 32px;
						}
					}
				}
			}
		}
	}
	.arco-sanitizante{
		padding: 0 15px;
		margin-top: 30px;
		p{
			font-size: 14px;
			&.title{
				color: #00a89c;
				font-weight: 500;
				text-align: center;
				font-size: 12px;
				&-alt{
					@extend .title; 
					font-size: 14px
				}
			}
			&.claimer-image{
				width: 60%;
				margin: 10px auto 0; 
				display:  block;
				text-align: center;
			}
		}
		ul{
			padding: 0 15px;
			li{
				font-size: 12px;
				text-align: center;
				max-width: 60%;
				margin: auto;
				img{
					margin-bottom: 10px;
				}
			}
		}
		.arco{
			margin: 0 auto; 
			display: block;
			width: 60%;
			position: relative;
		}
		@media (min-width: 768px){
			p{
				&.title{
					text-align: left;
				}
				&.claimer-image{
					position: absolute;
					bottom: 30px;
					margin: 0 0 0 -60%;
					text-align: left;
					font-size: 13px;
					width: 70%;
				}
			}
			ul{
				padding: 0;
				li{
					max-width: 100%;
					text-align: left;
				}
			}
			.arco{
				width: 75%;
			}
		}
		@media (min-width: 991px){
			margin-top: 60px;
			p{
				font-size: 14px;
				&.title{
					font-size: 16px;
				}
				&.claimer-image{
					bottom: 40px;
					width: 70%;
					margin: 0 0 0 -65%;
					font-size: 16px;
				}
			}
			ul{
				li{
					font-size: 14px;
				}
				&.two-column{
					li{
						.icons{
							width: 20px;
						}
					}
				}
			}
		}
		@media (min-width: 1200px){
			margin-top: 60px;
			p{
				font-size: 18px;
				&.title{
					font-size: 19px;
				}
				&.claimer-image{
					font-size: 21px;
					bottom: 70px;
				}
			}
			ul{
				li{
					font-size: 18px;
				}
			}
			.arco{
				width: 85%;
			}
		}
	}
	.liquido-desinfectante{
		padding: 0 15px;
		margin-top: 30px;
		p{
			font-size: 14px;
			&.title{
				color: #00a89c;
				font-weight: 500;
				text-align: center;
				font-size: 12px;
				&-alt{
					@extend .title; 
					font-size: 14px
				}
			}
			&.claimer-image{
				width: 60%;
				margin: 10px auto 0; 
				display:  block;
				text-align: center;
			}
		}
		ul{
			padding: 0 15px;
			li{
				font-size: 12px;
				text-align: center;
				max-width: 60%;
				margin: auto;
				img{
					margin-bottom: 10px;
				}
			}

		}
		.liquido{
			margin: 0 auto;
			display: block;
			width: 60%;
			position: relative;
		}
		@media (min-width: 768px){
			p{
				&.title{
					text-align: left;
				}
			}
			ul{
				padding: 0;
				li{
					max-width: 100%;
					text-align: left;
					font-size: 14px;
				}
			}
			.liquido{
				width: 100%;
			}
		}
		@media (min-width: 1200px){
			p{
				font-size: 18px;
				&.title{
					font-size: 19px
				}
			}
			ul{
				padding: 0;
				li{
					max-width: 100%;
					text-align: left;
					font-size: 18px;
				}
			}
			.liquido{
				width: 100%;
			}
		}
	}
	.otros-productos{
		padding: 0 15px;
		margin-top: 30px;
		.owl-carousel{
			.item{
				img{
					width: 50%;
					margin: auto;
					display: block;
				}
				.titulo{
					color: #5b86b1;
					font-size: 12px;
					text-align: center;
					font-weight: 800;
					margin-top: 10px;
				}
			}
		}
		.owl-theme .owl-dots .owl-dot span{
			background: #5b86b1;
			opacity: .5;
		}
		.owl-theme .owl-dots .owl-dot.active span,
		.owl-theme .owl-dots .owl-dot:hover span{
			opacity: 1;
		}
		@media (min-width: 768px){
			.owl-carousel{
				.item{
					img{
						width: auto;
						height: 180px;
					}
					.titulo{
						font-size: 14px;
					}
				}
			}
		}
		@media (min-width: 991px){
			.owl-carousel{
				margin-top: 30px;
				.item{
					.titulo{
						font-size: 16px;
						padding: 0 40px;
					}
				}
			}
		}
		@media (min-width: 1200px){
			.owl-carousel{
				margin-top: 30px;
				.item{
					.titulo{
						font-size: 22px;
					}
				}
			}
		}
	}
}
.contacto{
	position: relative;
	.col-12{
		padding: 0;
		line-height: 0;
	}
	&-claimer{
		position: absolute;
		width: 50%;
		top: 90px;
		left: 25%;
		p{
			color: #5b86b1;
			line-height: initial;
			font-size: 14px;
			text-align: center;
			font-weight: 700;
		}
		.btn-primary{
			background-color: #00a89c;
			border: none;
			outline: none;
			margin: 20px auto 0; 
			width: 50%;
			font-size: 13px;
			display: block;
		}
	}
	@media (min-width: 720px){
		.container{
			max-width: 100%;
		}
		&-claimer{
			top: 40%;
			p{
				font-size: 30px;
			}
			.btn-primary{
				margin: 40px auto 0;
				font-size: 16px;
			}
		}
	}
	@media (min-width: 991px){
		.container{
			max-width: 100%;
		}
		&-claimer{
			p{
				font-size: 40px;
			}
			.btn-primary{
				font-size: 20px;
			}
		}
	}
	@media (min-width: 1200px){
		.container{
			max-width: 100%;
		}
		&-claimer{
			p{
				font-size: 55px;
			}
			.btn-primary{
				font-size: 30px;
				width: 40%;
			}
		}
	}
}




























