// Load Fonts
@import url('https://fonts.googleapis.com/css?family=Roboto');

// Global Variables
$width: 1140;
$height: 1536;


body {
	font-family: 'Helvetica Neue', sans-serif;
	background-color: #efefef ;
}

.absolute {
	position: absolute;
}

.w-100 {
	width: 100%;
}

.d-block {
	display: block;
}

.bg {
	&-bolitas {
		top: 0;
		left: 0;
	}
}

.nav {
	&-bg {
		width: percentage(((380 * 100) / $width)* 0.01);
		left: percentage(((833 * 100) / $width)* 0.01);
		top: percentage(((1371 * 100) / $height)* 0.01);
	}
}
